import axios from "axios"
import { BASE_URL, TIMEOUT } from "./config"

//  axios 
class JWZRequestConfig {
    constructor(baseURL, timeout) {
        this.instance = axios.create({
            baseURL: baseURL,
            timeout: timeout
        })

        this.instance.interceptors.request.use((config) => {
            const token = localStorage.getItem("token") // BOM   js  DOM  BOM
                || sessionStorage.getItem("token")
                || localStorage.getItem("cookie")
                || sessionStorage.removeItem("cookie")

            if (token) {
                config.headers.Authorization = token
            }
            return config
        }, err => {
            // console.log(err)
        })

        // this.instance.interceptors.response.use((res) => {
        //     return res?.data
        // }, (err) => {
        //     if (err?.response) {
        //         switch (err?.response?.status) {
        //             // 
        //             case 401:
        //                 return Promise.reject(err?.response?.desc)
        //             case 403:
        //                 return Promise.reject(err?.response?.desc)
        //             case 404:
        //                 return Promise.reject(err?.response?.desc)
        //             case 500:
        //                 return Promise.reject(" |  fullStack")
        //             default:
        //                 return Promise.reject(err?.response?.desc)
        //         }
        //     } else if(err?.request) {
        //         return Promise.reject(err?.request)  // 
        //     } else {
        //         return Promise.reject(err)  // 
        //     }
        // })
    }

    request(config) {
        return new Promise((resolve, reject) => {
            this.instance.request(config).then(response => {
                resolve(response)
            }).catch(error => {
                // reject(error)
                // console.log(error)
            })
        })
    }

    get(config) {
        this.request({ ...config, method: 'GET' })
    }

    post(config) {
        this.request({ ...config, method: 'POST' })
    }

    put(config) {
        this.request({ ...config, method: 'PUT' })
    }

    patch(config) {
        this.request({ ...config, method: 'PATCH' })
    }

    delete(config) {
        this.request({ ...config, method: 'DELETE' })
    }
}

const JWZRequest = new JWZRequestConfig(BASE_URL, TIMEOUT)
export default JWZRequest

